 	    }
.sample-container {
      display: block;
      position: relative;
      cursor:grabbing;
      font-size: 15px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }

    /* Hide the browser's default checkbox */
    .sample-container input {
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }
		/* Create a custom checkbox */
		.checkmark-sample {
			position: relative;
			float: right;
			top: 0;
			left: 0;
			height: 20px;
			width: 20px;
			border-radius: 5px;
			background-color: #F5F5F5;
			border: 1px solid #A3A3A3;
		}

		/* On mouse-over, add a grey background color */
		.sample-container:hover input ~ .checkmark-sample {
			background-color: white;
		}

		/* When the checkbox is checked, add a blue background */
		.sample-container input:checked ~ .checkmark-sample {
			background-color: #2EC4B6;
		}

		/* Create the checkmark/indicator (hidden when not checked) */
		.checkmark-sample:after {
			content: "";
			display: none;
		}

		/* Show the checkmark when checked */
		.sample-container input:checked ~ .checkmark-sample:after {
			display: block;

		}

		/* Style the checkmark/indicator */
		.sample-container .checkmark-sample:after {
			position: relative;
			left: 6px;
			top: 0px;
			width: 5px;
			height: 15px;
			border: solid white;
			border-width: 0 3px 3px 0;
			-webkit-transform: rotate(45deg);
			-ms-transform: rotate(45deg);
			transform: rotate(45deg);
		}
		/* grippy */
	span.grippy {
	content: '....';
	width: 8px;
	height: 12px;
	display: inline-block;
	overflow: hidden;
	line-height: 3px;
	padding: 3px 4px;
	cursor: move;
	vertical-align: middle;
	margin-top: -.7em;
	font-size: 15px;
	font-family: 'Montserrat', sans-serif;
	letter-spacing: 2px;
	color: #114B5F;
	text-shadow: 1px 0 1px black;
}
span.grippy::after {
	content: '.. .. .. ..';
}
