#group-box{
  display: flex;
  justify-content: center;
	margin:auto;
}
[data-draggable="target"]
{
    list-style-type:none;
    width:378px;
    height:95px;
    overflow-y:auto;
    /* margin:0 0.5em 0.5em 0; */
    padding:0.5em;
    border-radius:15px;
    background-color: #F5F5F5;
    border: 1px solid #E5E5E5;
}

/* drop target state */
[data-draggable="target"][aria-dropeffect="move"]
{
    border-color:#FF8C42;

    background:#FF8C42;
}

/* drop target focus and dragover state */
[data-draggable="target""item"][aria-dropeffect="move"]:focus,
[data-draggable="target"][aria-dropeffect="move"].dragover
{
    outline:none;

    box-shadow:0 0 0 1px #fff, 0 0 0 3px #FF8C42;
}

/* draggable items */
[data-draggable="item"]
{
    display:block;
    list-style-type:none;

    margin:0 0 2px 0;
    padding:0.2em 0.4em;

    border-radius:0.2em;

    line-height:1.3;
}

/* items focus state */
[data-draggable="item"]:focus
{
    outline:none;
    box-shadow:0 0 0 2px #FF8C42, inset 0 0 0 1px #ddd;
}

/* items grabbed state */
[data-draggable="item"][aria-grabbed="true"]
{
    background:#FF8C42;
    color:#FF8C42;
}
